import Layout from '../Layout'
import { useSelector } from 'react-redux'

const About = ()=>{
	
	const posts = useSelector(state=>state.posts?.posts)
	
	const listPosts = ()=>{
		return posts && posts.map((item, index)=>{
			return <li key={index}>{item.title}</li>
		})
	}
	
    return (
        <Layout>
            About page
			<ol>
			{listPosts()}
			</ol>
        </Layout>
    )
}

export default About;