import axios from 'axios'
import { useEffect, useState } from 'react'
import Loading from 'react-fullscreen-loading';
import socketIO from 'socket.io-client'
import { Bar } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const io = socketIO('http://192.168.5.25:4000')

const Section = (props)=>{
	let title_job=0
	let title_pallet=0
	const {sub_name} = props.match.params
	const sub_name_array = sub_name.split(',')

	const [orders, setOrders] = useState([])
	const [inbounds, setInbounds] = useState([])
	const [jobComing, setJobComing] = useState([])
	const [palletCount, setPalletCount] = useState([])
	
		
	const data = {
	  labels: ['A1', 'A2', 'B', 'C', 'D', 'E','F', 'G','GMP', 'H','I','J','K','OEM','FG','PS'],
	  datasets: [
		{
		  label: 'จำนวนพาเลทในออเดอร์',
		  data: [],
		  backgroundColor: [
			'#990000',
			'#990000',
			'#522e1a',
			'#5a5b60',
			'#db3d7b',
			'#ffde2c',
			'#f36e31',
			'#333',
			'#000000',
			'#1b2c55',
			'#00ff00',
			'#145139',
			'#5aaebd',
			'#333',
			'#333',
			'#333',
		  ],
		  borderWidth: 1,
		},
	  ],
	};
	
	const options = {
		plugins: {
			datalabels: {
				font:{
					size:20,
				},
				display: true,
				color: 'white'
			}
		}
	}
	
	//data.labels = [12, 19, 3, 5, 2, 3,12, 19, 3, 5, 2, 3,4,2,1,5]
	//data.datasets[0].data = [12, 19, 3, 5, 2, 3,12, 19, 3, 5, 2, 3,4,2,1,5]
	const sub_name_arrr = palletCount.map((item)=>{
		return item.sub_name;
	})
	const total_pallet_arr = palletCount.map((item)=>{
		return item.total_pallet;
	})
	data.labels = sub_name_arrr
	data.datasets[0].data = total_pallet_arr
	
	const listSubname = ()=>{
		return sub_name_array.map((item, index)=>{
			return <span key={index} className={`title_color_bg ${item.toUpperCase()}`}>{item.toUpperCase()}</span>
		})
	}

	useEffect(()=>{
		axios.get('http://192.168.5.25:4000/pallet/get_count_pallet_in_zone/'+sub_name)
		.then((res)=>{
			if(res.data.success){
				setPalletCount(res.data.data)
			}
		})
	},[orders])
	
	useEffect(()=>{
		axios.get('http://192.168.5.25:4000/pallet/get_order_with_section/'+sub_name)
		.then((res)=>{
			if(res.data.success){
				setOrders(res.data.data)
			}
		})
		axios.get('http://192.168.5.25:4000/pallet/get_count_inbound_tv/'+sub_name)
		.then((res)=>{
			if(res.data.success){
				setInbounds(res.data.data)
			}
		})
		axios.get('http://192.168.5.25:4000/pallet/get_count_job_coming_tv/'+sub_name)
		.then((res)=>{
			if(res.data.success){
				setJobComing(res.data.data)
			}
		})
		
		//
		io.on("onRefresh",(data)=>{
			console.log('Live data')
			axios.get('http://192.168.5.25:4000/pallet/get_order_with_section/'+sub_name)
			.then((res)=>{
				if(res.data.success){
					setOrders(res.data.data)
				}
			})
		})
		
		io.on("onRefreshInbound",(data)=>{
			axios.get('http://192.168.5.25:4000/pallet/get_count_inbound_tv/'+sub_name)
			.then((res)=>{
				if(res.data.success){
					setInbounds(res.data.data)
				}
			})
		})
		io.on("onRefreshJobsComing",(data)=>{
			axios.get('http://192.168.5.25:4000/pallet/get_count_job_coming_tv/'+sub_name)
			.then((res)=>{
				if(res.data.success){
					setJobComing(res.data.data)
				}
			})
		})
			
		},[sub_name])
	
	const listOrder = ()=>{
		return orders.map((item, index)=>{
			return (
				<tr key={index} className={item.is_manual===1?'first-priority':''}>
					<td>{index+1}</td>
					<td>{item.order_code}</td>
					<td className="left">{getWiLink(item.job_id)}</td>
					<td className="left"><div className="endtext">{item.part_name}</div></td>
					<td>{item.sig}</td>
					<td>{item.amount}</td>
					<td><span className={`div_zone_color_bg ${item.out_bound}`}>{item.out_bound}</span></td>
					<td className="left">{item.machine_process}</td>
					<td><span className={`div_zone_color_bg ${item.in_bound}`}>{item.in_bound}</span></td>
					<td className="left">{item.next_process}</td>
				</tr>
			)
		})
	}
	
	const listInbounds = ()=>{
		return inbounds.map((item, index)=>{
			return (
				<tr key={index}>
					<td>{index+1}</td>
					<td>{getWiLink(item.job_id)}</td>
					<td className="center">{item.count_pallet}</td>
					<td className="center"><span className={`div_zone_color_bg ${item.sub_name}`}>{item.sub_name}</span></td>
				</tr>
			)
		})
	}
	const listJobsComing = ()=>{
		return jobComing.map((item, index)=>{
			return (
				<tr key={index}>
					<td>{index+1}</td>
					<td>{getWiLink(item.job_id)}</td>
					<td className="center">{item.count_pallet}</td>
					<td className="center"><span className={`div_zone_color_bg ${item.sub_name}`}>{item.sub_name}</span></td>
				</tr>
			)
		})
	}
	
	const getWiLink = (job_id)=>{
			let job_no=job_id.split(".");
			job_no=job_no[0];
			const y=job_no.substr(2,2);
			const wi_link="http://192.168.5.40/linsip/wi/20"+y+"/"+job_no+".pdf";
			//window.open(wi_link, '_blank', 'noopener,noreferrer')
			return <a href={wi_link} target="_blank" className="a_link_wi">{job_id}</a>
	}
	
    return (
        <div>
            <div id="section-header">
				<div className="item">
					{listSubname()}
				</div>
			</div>
			 <div id="section-body">
					<div id="left">
						<div className="title">
							<div className="one-item">{listSubname()}</div>
							<div className="two-item">ตารางขนย้ายพาเลท</div>
						</div>
						<div className="subtitle">
							Order: <span>{orders.length}</span> Job: <span>{title_job}</span> พาเลท: <span>{title_pallet}</span>
						</div>
						<table>
							<thead>
								<tr>
									<th>#</th>
									<th>Order</th>
									<th>Job</th>
									<th>ชิ้นส่วน</th>
									<th>ยก</th>
									<th>จำนวน</th>
									<th>Out</th>
									<th>ขั้นตอน</th>
									<th>Inb</th>
									<th>ถัดไป</th>
								</tr>
							</thead>
							<tbody>
								{ listOrder()} 
							</tbody>
						</table>
						<Bar data={data} plugins={[ChartDataLabels]} options={options}/>
					</div>
					<div id="middle">
					
						<div className="title">
							รอผลิต
						</div>
						<table>
							<thead>
								<tr>
									<th>#</th>
									<th>Job</th>
									<th>พาเลท</th>
									<th>โซน</th>
								</tr>
							</thead>
							<tbody>
								{ listInbounds()} 
							</tbody>
						</table>
					
					</div>
					<div id="right">
					
						<div className="title">
							รอขนย้ายเข้า
						</div>
						<table>
							<thead>
								<tr>
									<th>#</th>
									<th>Job</th>
									<th>พาเลท</th>
									<th>โซน</th>
								</tr>
							</thead>
							<tbody>
								{ listJobsComing()} 
							</tbody>
						</table>
						
					</div>
			</div>
        </div>
    )
}

export default Section;